@charset "UTF-8";
/*
   `with-rem-to-pt-property` is designed to convert “rem” property values
   of screen styles to print media values expressed in points (“pt”).
*/
@page {
  margin: 24pt 12pt;
}
/*
   “Modern Georgia-based serif stack”
   https://css-tricks.com/snippets/css/font-stacks/
*/
html,
.pure-g [class*=pure-u] {
  font-family: Constantia, "Lucida Bright", Lucidabright, "Lucida Serif", Lucida, "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", Georgia, serif;
}

.resume-container {
  width: 100%;
  max-width: none;
  background: none;
}

.resume-header {
  border-bottom-width: 1pt;
  margin-bottom: 7.5pt;
  padding: 7.5pt;
  background: none;
  border-color: #666;
  color: #111;
}
.resume-header h1,
.resume-header h2 {
  font-family: Constantia, "Lucida Bright", Lucidabright, "Lucida Serif", Lucida, "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", Georgia, serif;
}
.resume-header h1 {
  font-size: 20pt;
}
.resume-header h2 {
  font-size: 12.5pt;
  padding-bottom: 5pt;
}

.resume-header-infos {
  font-size: 10pt;
  line-height: 10pt;
  margin-bottom: 7.5pt;
}
.resume-header-infos .resume-header-info {
  display: inline-block;
  padding-bottom: 2.5pt;
}
.resume-header-infos .resume-header-info .resume-header-info-link {
  text-decoration: none;
}
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-icon,
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-icon:visited,
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-icon:hover,
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-icon:active {
  fill: #aaa;
}
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-text,
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-text:visited,
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-text:hover,
.resume-header-infos .resume-header-info .resume-header-info-link .resume-header-info-text:active {
  color: #333;
}
.resume-header-infos .resume-header-info .resume-header-info-icon {
  vertical-align: text-bottom;
  height: 10pt;
  padding-right: 0.3125pt;
  width: 10pt;
}
.resume-header-infos .resume-header-info .resume-header-info-text {
  /*
     “Helvetica/Arial-based sans serif stack”
     https://css-tricks.com/snippets/css/font-stacks/
  */
  font-family: Frutiger, "Frutiger Linotype", Univers, Calibri, "Gill Sans", "Gill Sans MT", "Myriad Pro", Myriad, "DejaVu Sans Condensed", "Liberation Sans", "Nimbus Sans L", Tahoma, Geneva, "Helvetica Neue", Helvetica, Arial, sans-serif;
  vertical-align: middle;
  font-size: 10pt;
  padding-right: 5pt;
  line-height: 10pt;
}

.resume-header-social-networks {
  font-size: 10pt;
  line-height: 10pt;
}
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link {
  text-decoration: none;
  /*
     Print “data-print-text” after each link icon
  */
}
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link .resume-header-social-network-icon {
  vertical-align: text-bottom;
  height: 10pt;
  padding-right: 0.3125pt;
  width: 10pt;
}
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link .resume-header-social-network-icon,
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link .resume-header-social-network-icon:visited,
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link .resume-header-social-network-icon:hover,
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link .resume-header-social-network-icon:active {
  fill: #aaa;
}
.resume-header-social-networks .resume-header-social-network .resume-header-social-network-link::after {
  color: #333;
  content: attr(data-print-text);
  /*
     “Helvetica/Arial-based sans serif stack”
     https://css-tricks.com/snippets/css/font-stacks/
  */
  font-family: Frutiger, "Frutiger Linotype", Univers, Calibri, "Gill Sans", "Gill Sans MT", "Myriad Pro", Myriad, "DejaVu Sans Condensed", "Liberation Sans", "Nimbus Sans L", Tahoma, Geneva, "Helvetica Neue", Helvetica, Arial, sans-serif;
  vertical-align: middle;
  font-size: 10pt;
  line-height: 10pt;
  padding-right: 5pt;
}

.resume-headline {
  background: none;
  background-color: #ddd;
  color: #111;
  color-adjust: exact;
  width: 90%;
  border-radius: 7.5pt;
  padding: 7.5pt;
}
.resume-headline p {
  font-style: oblique;
  font-size: 10pt;
  line-height: 15pt;
  margin-top: 7.5pt;
  margin-bottom: 7.5pt;
}

.resume-section-work-experience,
.resume-section-skills,
.resume-section-languages,
.resume-section-education {
  background: none;
  padding-left: 0;
  padding-right: 0;
}

.resume-section-top {
  background-color: #ddd;
  border-width: 0;
  border-radius: 0;
  color: #111;
  color-adjust: exact;
  padding: 7.5pt;
}
.resume-section-top h1 {
  font-size: 10pt;
}

.resume-education,
.resume-languages,
.resume-skills,
.resume-work-experience {
  background: none;
  border-radius: 0;
  border: 0;
  color: #111;
  font-size: 8pt;
  padding: 7.5pt;
}
.resume-education h2,
.resume-languages h2,
.resume-skills h2,
.resume-work-experience h2 {
  font-size: 10pt;
  padding-bottom: 2.5pt;
}
.resume-education p,
.resume-languages p,
.resume-skills p,
.resume-work-experience p {
  line-height: 10pt;
  margin-bottom: 5pt;
  margin-top: 5pt;
}

.resume-language,
.resume-skill {
  padding-bottom: 1.25pt;
}

.resume-education-item,
.resume-work {
  padding-bottom: 5pt;
}

.resume-education-degree-major,
.resume-language-fluency,
.resume-skill-keywords,
.resume-work-employer-name {
  color: #333;
}

.resume-education-infos,
.resume-work-infos {
  padding-bottom: 2pt;
}

.resume-education-timeframe-icon,
.resume-education-degree-name-icon,
.resume-work-timeframe-icon,
.resume-work-employer-location-icon {
  height: 8pt;
  padding-right: 0.25pt;
  width: 10pt;
  color: #aaa;
}

.resume-education-timeframe-text,
.resume-education-degree-name-text,
.resume-work-timeframe-text,
.resume-work-employer-location-text {
  padding-right: 4pt;
  color: #333;
}

.resume-work-keywords {
  padding-bottom: 1.25pt;
}
.resume-work-keywords .resume-work-keyword {
  background-color: #ddd;
  color: #555;
  color-adjust: exact;
  /*
       “Helvetica/Arial-based sans serif stack”
       https://css-tricks.com/snippets/css/font-stacks/
    */
  font-family: Frutiger, "Frutiger Linotype", Univers, Calibri, "Gill Sans", "Gill Sans MT", "Myriad Pro", Myriad, "DejaVu Sans Condensed", "Liberation Sans", "Nimbus Sans L", Tahoma, Geneva, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 7.25pt;
  line-height: 7.25pt;
  border-radius: 4pt;
  margin-right: 0.625pt;
}